<template>
  <div class="embed-responsive embed-responsive-1by1">
    <iframe
      @load="load"
      class="embed-responsive-item"
      :src="links.manualProcedimientos"
    ></iframe>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  created() {
    this.SET_TITLE("Manual de Procedimientos");
  },
  mounted() {
    this.loader = this.$loading.show();
  },
  beforeRouteLeave(to, from, next) {
    // Oculta el loader antes de cambiar de pesta√±a
    this.loader.hide();
    next();
  },
  computed: {
    ...mapState(["links"])
  },
  methods: {
    ...mapMutations(["SET_TITLE"]),
    load() {
      this.loader.hide();
    }
  }
};
</script>

<style></style>
